import React, { useEffect, useState } from 'react';
import {
  Row,
  Card,
  CardBody,
  FormGroup,
  Label,
  Button,
  CardTitle,
} from 'reactstrap';
import { Formik, Form, Field } from 'formik';
import { connect } from 'react-redux';
import { Colxx } from '../../components/common/CustomBootstrap';
import IntlMessages from '../../helpers/IntlMessages';
import { addProfileData } from '../../redux/actions';
import { NotificationManager } from '../../components/common/react-notifications';

const validateName = (value) => {
  let error;
  if (!value) {
    error = 'Please enter your name';
  } else if (value.length < 2) {
    error = 'Value must be longer than 2 characters';
  }
  return error;
};

const CompleteRegistration = ({
  history,
  loading,
  error,
  currentUser,
  addProfileAction,
}) => {
  const [fullName] = useState('');

  const onAddProfile = (values) => {
    const { id, phone_number: phoneNumber } = currentUser;
    const profile = {
      ...values,
      userID: id,
      contactNo: phoneNumber,
    };

    addProfileAction({ profile, history });
  };

  useEffect(() => {
    if (error) {
      console.log(error);
      NotificationManager.warning(
        error,
        'OTP Verification Error',
        3000,
        null,
        null,
        ''
      );
    }
  }, [loading, error]);

  const initialValues = {
    fullName,
  };

  return (
    <Row className="h-100">
      <Colxx xxs="12" md="8" className="mx-auto my-auto">
        <Card>
          <CardBody>
            <CardTitle className="mb-4">
              <h2 className="lead">
                <IntlMessages id="user.fullname" />
              </h2>
            </CardTitle>
            <Formik initialValues={initialValues} onSubmit={onAddProfile}>
              {({ errors, touched }) => (
                <Form className="av-tooltip tooltip-label-right">
                  <FormGroup>
                    <Label>
                      <IntlMessages id="user.fullname" />
                    </Label>
                    <Field
                      className="form-control"
                      name="fullName"
                      validate={validateName}
                    />
                    {errors.name && touched.name && (
                      <div className="invalid-feedback d-block">
                        {errors.name}
                      </div>
                    )}
                  </FormGroup>
                  <div className="d-flex justify-content-center align-items-center">
                    <Button
                      color="primary"
                      className={`btn-shadow btn-multiple-state ${
                        loading ? 'show-spinner' : ''
                      }`}
                      size="lg"
                    >
                      <span className="spinner d-inline-block">
                        <span className="bounce1" />
                        <span className="bounce2" />
                        <span className="bounce3" />
                      </span>
                      <span className="label">
                        <IntlMessages id="forms.submit" />
                      </span>
                    </Button>
                  </div>
                </Form>
              )}
            </Formik>
          </CardBody>
        </Card>
      </Colxx>
    </Row>
  );
};
const mapStateToProps = ({ authUser }) => {
  const { loading, error, currentUser } = authUser;
  return { loading, error, currentUser };
};

const mapActionToProps = {
  addProfileAction: addProfileData,
};

export default connect(mapStateToProps, mapActionToProps)(CompleteRegistration);
